body {
  font-family: "Roboto", sans-serif;
}

main {
  margin-top: 71px;
  margin-left: 82px;
}

h3 {
  font-size: 22px;
}

/*///////////////////////////////////////////////
// NAV STYLES
///////////////////////////////////////////////*/

.container {
  padding-left: 300px;
}

@media only screen and (max-width: 992px) {
  .container {
    padding-left: 0;
  }
}

/*///////////////////////////////////////////////
// HOME PAGE STYLES
///////////////////////////////////////////////*/

.user-character:hover {
  cursor: pointer;
  font-style: italic;
}

.selected-character {
  font-weight: bold;
}

/*///////////////////////////////////////////////
// ITEM PAGE STYLES
///////////////////////////////////////////////*/

label {
  color: black;
}

/* a.room-complete {
  color: green !important;
} */

.room-header span {
  font-size: 22px;
}

.room-subheader {
  font-size: 16px;
  margin: 0 0 0 30px;
}

.bundle {
  margin-left: 30px;
}

.bundle-header span {
  font-size: 16px;
}

.bundle-subheader {
  font-size: 14px;
  margin: 0 0 0 30px;
}

.bundle-item {
  margin-left: 30px;
}

.gray-out span {
  color: rgb(199, 199, 199);
}

/*///////////////////////////////////////////////
// CALENDAR PAGE STYLES
///////////////////////////////////////////////*/

.calendar-day {
  font-size: 18px;
  font-weight: bold;
}

p.calendar-event-name {
  margin: 0;
  font-size: 16px;
}

.calendar-desc p {
  margin: 0 0 5px 0;
}

/*///////////////////////////////////////////////
// SEASON STYLES
///////////////////////////////////////////////*/

.season-filter-btn:hover {
  cursor: pointer;
}

.season-filter-btn {
  height: 40px !important;
  width: 40px !important;
}

.season-filter-container {
  height: 50px;
  width: 50px;
  padding: 5px;
  background: rgb(247, 247, 247);
}

.season-filter-active {
  background: rgb(189, 189, 189);
}



